 <template>
  <el-main style="text-align:left; line-height: 1.8em;">

    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" :rules="rules"  label-width="80px" class="demo-ruleForm">
        <el-form-item label="" prop="projectName">
          <el-input v-model="form.projectName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ContractDate">
          <el-col :span="11">
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="form.ContractDate" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="CustomerName">
          <el-select v-model="form.CustomerNameid" filterable placeholder="" style="width:46%">
                <el-option
                v-for="item in optionsproject"
                :key="item.CustomerName"
                :label="item.CustomerName"
                :value="item.id">
                </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ContractAmount">
          <el-input type="number"  v-model.number="form.ContractAmount" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="ExpenditureBudget">
          <el-input type="number" v-model.number="form.ExpenditureBudget" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="projectContent">
          <el-input type="textarea" v-model="form.projectContent" ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')"></el-button>
          <el-button @click="dialogFormVisible = false"></el-button>
          <el-button style="float:right" @click="deletepost"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <h3>
      
      <el-button type="primary" style="float: right; margin-left:10px;" @click="dialogFormVisible = true"></el-button>
      <el-button type="primary" style="float: right; margin-left:10px;" ></el-button>
      <el-button type="primary" style="float: right; margin-left:10px;" @click="ReceivablesGo"></el-button>
      <el-button type="primary" style="float: right; margin-left:10px;" @click="expenditureGo"></el-button>
    </h3>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="pojname" label="" width="120"></el-table-column>
      <el-table-column prop="contents" label="" ></el-table-column>
    </el-table>
    <h3></h3>
    <el-table :data="shoukuan" border style="width: 100%">
      <el-table-column prop="ReceivablesName" label="" sortable></el-table-column>
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesData" label="" sortable></el-table-column>
      <el-table-column prop="contract" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesMode" label="" sortable></el-table-column>
      <el-table-column prop="Remarks" label="" sortable></el-table-column>
    </el-table>
    <h3></h3>
    <el-table :data="tableDatayingjiang1" border style="width: 100%">
      <el-table-column prop="SupplierName" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesName" label="" sortable></el-table-column>
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesData" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="projectClass" label="" sortable></el-table-column>
      <el-table-column prop="Remarks" label="" sortable></el-table-column>
    </el-table>
    <h3></h3>
    <el-table :data="tableDataruangjian" border style="width: 100%">
      <el-table-column prop="SupplierName" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesName" label="" sortable></el-table-column>
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesData" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="projectClass" label="" sortable></el-table-column>
      <el-table-column prop="Remarks" label="" sortable></el-table-column>
    </el-table>
        <h3></h3>
    <el-table :data="gongcheng" border style="width: 100%">
      <el-table-column prop="SupplierName" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesName" label="" sortable></el-table-column>
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesData" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="projectClass" label="" sortable></el-table-column>
      <el-table-column prop="Remarks" label="" sortable></el-table-column>
    </el-table>
        <h3></h3>
    <el-table :data="yewu" border style="width: 100%">
      <el-table-column prop="SupplierName" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesName" label="" sortable></el-table-column>
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesData" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="projectClass" label="" sortable></el-table-column>
      <el-table-column prop="Remarks" label="" sortable></el-table-column>
    </el-table>
    <h3></h3>
    <el-table :data="tableDatatuozhan" border style="width: 100%">
      <el-table-column prop="SupplierName" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesName" label="" sortable></el-table-column>
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesData" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="projectClass" label="" sortable></el-table-column>
      <el-table-column prop="Remarks" label="" sortable></el-table-column>
    </el-table>
  </el-main>
</template>
<style>
</style>

<script>
export default {
  data () {
    return {
      shoukuan: [],
      gongcheng: [],
      yewu: [],
      optionsprojectClass: [],
      optionsOtherParty: [],
      zhongshouru: '',
      weishoukuan: '',
      projectName: '',
      rules: {
        projectName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        projectContent: [
          { required: true, message: '', trigger: 'blur' }
        ],
        CustomerName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        ContractAmount: [
          { required: true, message: '' },
          { type: 'number', message: '' }
        ],
        ContractDate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        ExpenditureBudget: [
          { required: true, message: '' },
          { type: 'number', message: '' }
        ]
      },
      Receivables: [],
      expenditure: [],
      optionsproject: [],
      dialogFormVisible: false,
      form: {
        projectName: '',
        projectContent: '',
        CustomerName: '',
        ContractAmount: '',
        ContractDate: '',
        ExpenditureBudget: ''
      },
      contentsinvoice: '',
      contentsint: '',
      Receivablesint: '',
      contentsintp: '',
      Receivablesintp: '',
      invoiceint: '',
      invoiceintp: '',
      tableDatayingjiang: [],
      tableDatayingjiang1: [],
      tableDataruangjian: [],
      tableDatatuozhan: [],
      tableData: [
        {
          pojname: '',
          contents: ''
        },
        {
          pojname: '',
          contents: ''
        },
        {
          pojname: '',
          contents: ''
        },
        {
          pojname: '',
          contents: ''
        },
        {
          pojname: '',
          contents: ''
        },
        {
          pojname: '',
          contents: ''
        },
        {
          pojname: '',
          contents: ''
        },
        {
          pojname: '',
          contents: ''
        }
      ]
    }
  },
  mounted () {
    this.getdata()
    this.getFormData('SupplierList')
    this.getFormDataClass('expenditureClass')
  },
  methods: {

    getdata () {
      this.jsondata.getDataId('projectList', this.$route.params.id).then(response => {
        // console.log(response.data[0].ContractDate)
        this.form = response.data[0]
        this.form.ContractDate = response.data[0].ContractDate.substr(0, 10)
        this.tableData[0].contents = this.form.projectName
        this.tableData[1].contents = this.form.ContractDate
        this.tableData[4].contents = ':' + this.jsondata.currency(this.form.ExpenditureBudget, '', 2)
        this.tableData[7].contents = this.form.projectContent
        this.getdataReceivables()
        this.jsondata.getData('Customerlist').then(response => { // 
          this.optionsproject = response.data
          for (let is = 0; is < response.data.length; is++) {
            if (this.form.CustomerName == response.data[is].id) { //eslint-disable-line
              this.form.CustomerNameid = response.data[is].CustomerName // 
              this.tableData[2].contents = this.form.CustomerNameid
            }
          }
        })
          .catch(error => {
            console.log(error)
          })
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    updatpostData () { // 
      if (Number.isInteger(this.form.CustomerNameid)) {
        this.form.CustomerName = this.form.CustomerNameid
      }
      // console.log(this.form.CustomerNameid)
      this.jsondata.updatpostData('projectList', this.form).then(response => {
        if (response.data === 'OK') {
          this.dialogAddVisible = false
          this.dialogFormVisible = false
          this.getdata()
        }
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updatpostData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    deletepost () { // 
      this.jsondata.deletepost('projectList', this.$route.params.id).then(response => {
        // console.log(response.data)
        if (response.data === 'OK') {
          this.$router.push('/index')
        }
      })
        .catch(error => {
          console.log(error)
        })
    },
    ReceivablesGo () {
      this.$router.push('/Receivables/' + this.$route.params.id)
    },
    expenditureGo () {
      this.$router.push('/expenditure/' + this.$route.params.id)
    },
    getdataReceivables () {
      this.jsondata.getDataClass('Receivables', this.$route.params.id, 'projectId').then(response => {
        if (response.data.length == 0) { //eslint-disable-line
          // this.tableData[3].contents += ' / : 0'
          // console.log(response.data.length)
        } else {
          // console.log(response.data[0].number)
          for (let i = 0; i < response.data.length; i++) {
            this.contentsint = Number(this.contentsint) + Number(response.data[i].number)
            if(response.data[i].contract == ''){ //eslint-disable-line
              this.contentsinvoice = Number(this.contentsinvoice) + Number(response.data[i].number)
            }
          }
          for (let i = 0; i < response.data.length; i++) {
            this.Receivablesint = Number(this.Receivablesint) + Number(response.data[i].Receivables)
            this.invoiceint = Number(this.invoiceint) + Number(response.data[i].invoice)
          }
          this.tableData[3].contents = ':' + this.jsondata.currency(this.contentsinvoice, '', 2)
          this.tableData[3].contents += ' / :' + this.jsondata.currency(this.contentsint, '', 2)
          this.tableData[3].contents += ' / :' + this.jsondata.currency(this.Receivablesint, '', 2)
          this.tableData[3].contents += ' / :' + this.jsondata.currency(this.contentsint - this.Receivablesint, '', 2)
          this.tableData[5].contents = ':' + this.jsondata.currency(this.invoiceint, '', 2)
          this.tableData[6].contents = this.jsondata.currency(this.Receivablesint, '', 2)
          this.getdataexpenditure()
          this.shoukuan = response.data
          for (let i = 0; i < this.shoukuan.length; i++) {
            this.shoukuan[i].number = this.jsondata.currency(this.shoukuan[i].number, '', 2)
            this.shoukuan[i].Receivables = this.jsondata.currency(this.shoukuan[i].Receivables, '', 2)
            this.shoukuan[i].invoice = this.jsondata.currency(this.shoukuan[i].invoice, '', 2)
          }
        }
        // this.tableData[3].contents
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    getdataexpenditure () {
      this.jsondata.getDataClass('expenditure', this.$route.params.id, 'projectId').then(response => {
        if (response.data.length == 0) { //eslint-disable-line
          // this.tableData[4].contents += '|: 0'
          // console.log(response.data.length)
        } else {
          // console.log(response.data[0].number)
          for (let i = 0; i < response.data.length; i++) {
            this.contentsintp = Number(this.contentsintp) + Number(response.data[i].number)
          }
          this.tableData[4].contents += ' / :' + this.jsondata.currency(this.contentsintp, '', 2)
          for (let i = 0; i < response.data.length; i++) {
            this.Receivablesintp = Number(this.Receivablesintp) + Number(response.data[i].Receivables)
            this.invoiceintp = Number(this.invoiceintp) + Number(response.data[i].invoice)
          }
          this.tableData[4].contents += ' / :' + this.jsondata.currency(this.Receivablesintp, '', 2)
          this.tableData[4].contents += ' / :' + this.jsondata.currency(this.contentsintp - this.Receivablesintp, '', 2)
          this.tableData[5].contents += ' / :' + this.jsondata.currency(this.invoiceintp, '', 2)
          // console.log(this.Receivablesint, this.Receivablesintp)
          this.tableData[6].contents = this.jsondata.currency(this.Receivablesint - this.Receivablesintp, '', 2)
        }
        this.getdatae1()
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    getdatae1 () {
      this.jsondata.getDataClass('expenditure', this.$route.params.id, 'projectId').then(response => {
        this.tableDatayingjiang = response.data
        for (let i = 0; i < this.tableDatayingjiang.length; i++) {
          this.zhongshouru = Number(this.zhongshouru) + Number(this.tableDatayingjiang[i].Receivables)
          this.weishoukuan = Number(this.weishoukuan) + Number(this.tableDatayingjiang[i].number)
          this.tableDatayingjiang[i].number = this.jsondata.currency(this.tableDatayingjiang[i].number, '', 2)
          this.tableDatayingjiang[i].Receivables = this.jsondata.currency(this.tableDatayingjiang[i].Receivables, '', 2)
          this.tableDatayingjiang[i].invoice = this.jsondata.currency(this.tableDatayingjiang[i].invoice, '', 2)
        }
        this.weishoukuan = Number(this.weishoukuan) - Number(this.zhongshouru)
        this.zhongshouru = this.jsondata.currency(this.zhongshouru, '', 2)
        this.weishoukuan = this.jsondata.currency(this.weishoukuan, '', 2)
        this.jsondata.getData('SupplierList').then(response => {
          for (var i = 0; i < this.tableDatayingjiang.length; i++) {
            for (let is = 0; is < response.data.length; is++) {
              if (this.tableDatayingjiang[i].OtherParty == response.data[is].SupplierClass) { //eslint-disable-line
                this.tableDatayingjiang[i].SupplierName = response.data[is].SupplierName
                console.log(this.tableDatayingjiang[i].projectClass)
                if(this.tableDatayingjiang[i].projectClass == ''){ //eslint-disable-line
                  this.tableDatayingjiang1.push(this.tableDatayingjiang[i])
                }
                if(this.tableDatayingjiang[i].projectClass == ''){ //eslint-disable-line
                  this.tableDataruangjian.push(this.tableDatayingjiang[i])
                }
                if(this.tableDatayingjiang[i].projectClass == ''){ //eslint-disable-line
                  this.gongcheng.push(this.tableDatayingjiang[i])
                }
                if(this.tableDatayingjiang[i].projectClass == ''){ //eslint-disable-line
                  this.yewu.push(this.tableDatayingjiang[i])
                }
                if(this.tableDatayingjiang[i].projectClass == ''){ //eslint-disable-line
                  this.tableDatatuozhan.push(this.tableDatayingjiang[i])
                }
              }
            }
          }
        })
          .catch(error => {
            console.log(error)
          })
      })
        .catch(error => {
          console.log(error)
        })
    },
    getFormData (formName) { // 
      this.jsondata.getData(formName).then(response => {
        this.optionsOtherParty = response.data
      })
        .catch(error => {
          console.log(error)
        })
    },
    getFormDataClass (formName) { // 
      this.jsondata.getData(formName).then(response => {
        this.optionsprojectClass = response.data
      })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
</script>



